import React from 'react';
import Box from '@material-ui/core/Box'
import { Link } from 'react-router-dom';
import logo from './737414.svg';
import { useTheme } from '@material-ui/core';

export function Header() {
    const theme = useTheme();
    const links = ['tracker','logs','settings']
    const headerCssProps: React.CSSProperties = {
        width: "100vw",
        height: "32px",
        backgroundColor: theme.palette.primary.main,
    }
    const linkCssProps: React.CSSProperties = {
        margin: '8px'
    }
    return (
        <Box style={headerCssProps} display="flex">
            <img src={logo} height="100%" alt="sink" />
            {links.map((key:string) => 
                <Box style={linkCssProps}><Link key={key} to={`/sink/${key}`} className="fix" >{key}</Link></Box>
            )}
        </Box>
    );
}